import React from 'react'

const Navbar = ({noStates, setNoStates}) => {
    return (
        <nav className="navbar is-light mb-5" role="navigation" aria-label="main navigation">
            <div className="navbar-brand">
                <a className="navbar-item" href="https://namesrunout.github.io/rcalculator/">
                    <img src="data:image/svg+xml;base64," width="28" height="28" />
                </a>
            </div>

            <div className="navbar-menu is-active">
                <span className="navbar-item">
                    Number of states in the automata:
                    <input className='input is-small' type='text' value={noStates}
                               onChange={(e) => setNoStates(e.target.value)}/>
                </span>
            </div>
        </nav>
    )
}

export default Navbar;
